import React, { useEffect, useState } from 'react'
import { useSpring, animated } from 'react-spring'
import loadingImg from '../../img/loading.png'
import illustlist from '../../import/illust'

const IllustArray = () => {
  const illustImage = illustlist()

  const [selectedImage, setSelectedImage] = useState(null)
  const [scroll, setScroll] = useState('visible')
  const [loading, setLoading] = useState(true)

  document.body.style.overflow = scroll

  const openModal = (index) => {
    setSelectedImage(illustImage[index])
    setScroll('hidden')
  }

  const closeModal = () => {
    setSelectedImage(null)
    setScroll('visible')
  }

  const scaleAnimation = useSpring({
    transform: `scale(${selectedImage ? 1 : 0.5})`,
  })

  const backgroundAnimation = useSpring({
    opacity: selectedImage ? 1 : 0,
    backgroundColor: selectedImage ? 'rgba(0, 0, 0, 0.75)' : 'rgba(0, 0, 0, 0)',
  })

  //
  const download = async () => {
    const imageUrls = illustlist()

    try {
      await Promise.all(
        imageUrls.map((url) => {
          return new Promise((resolve) => {
            const IllustBox = new Image()
            IllustBox.src = url
            IllustBox.onload = () => {
              resolve(null)
            }
          })
        }),
      )
    } finally {
      // 
      setLoading(false)
    }
  }
  useEffect(() => {
    download()
  }, [])

  if (loading) {
    return (
      <div className="fixed left-1/2 top-1/2 z-50 -translate-x-1/2 -translate-y-1/2 transform">
        {/* <div className="h-10 w-10 animate-spin rounded-full border-4 border-popoYellow border-t-transparent"></div> */}
        <img
          src={loadingImg}
          alt=""
          className="h-10 w-10 animate-spin object-contain"
        />
      </div>
    )
  }

  return (
    <>
      {illustImage.map((image, index) => (
        <img
          key={index}
          src={image}
          alt={`msgImage ${index}`}
          className="transform cursor-pointer object-contain shadow transition-transform duration-300 hover:scale-105"
          onClick={() => openModal(index)}
        />
      ))}

      {selectedImage && (
        <div>
          <animated.div
            style={backgroundAnimation}
            className="fixed left-0 top-0 flex h-full w-full items-center justify-center"
            onClick={closeModal}
          >
            <animated.img
              src={selectedImage}
              alt="Selected Image"
              className=" object-contain px-4 sm:h-3/4"
              style={scaleAnimation}
            />
          </animated.div>
        </div>
      )}
    </>
  )
}

export default IllustArray
