import React from 'react'
import Modal from 'react-modal'
import { Link } from 'react-router-dom'

const ModalBtn = ({ modalIsOpen, closeModal }) => {
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      transition: 'opacity 200ms ease-in-out',
    },
    content: {
      width: 'auto',
      height: 'auto',
      maxWidth: '90%', // 90% ()
      maxHeight: '90%', // 90% ()
      margin: 'auto',
      backgroundColor: '#fef9c3',
      borderRadius: '20px',
      top: '90px', // 
      right: '20px', // 
      left: 'auto',
      bottom: 'auto',
      // transform: 'none', // transform
    },
  }

  return (
    <Modal
      isOpen={modalIsOpen}
      onRequestClose={closeModal}
      contentLabel=""
      style={customStyles}
      closeTimeoutMS={200}
    >
      <div className="  items-center justify-center lg:flex">
        <Link to={'/popo2024sample/Illust'} onClick={closeModal}>
          <IllustIcon />
        </Link>
        <Link to={'/popo2024sample/Message'} onClick={closeModal}>
          <MessageIcon />
        </Link>
        <Link to={'/popo2024sample/Photo'} onClick={closeModal}>
          <PhotoIcon />
        </Link>
      </div>
    </Modal>
  )
}

export default ModalBtn

const IllustIcon = () => {
  return (
    <div className="m-2  transform bg-popoYellow p-8 text-center text-green-600 shadow transition-transform duration-300 hover:scale-105 xl:text-3xl ">
      
    </div>
  )
}

const MessageIcon = () => {
  return (
    <div className="m-2 transform bg-popoYellow  p-8 text-center text-green-600 shadow transition-transform duration-300 hover:scale-105 xl:text-3xl">
      
    </div>
  )
}

const PhotoIcon = () => {
  return (
    <div className="m-2 transform bg-popoYellow  p-8 text-center text-green-600 shadow transition-transform duration-300 hover:scale-105 xl:text-3xl">
      
    </div>
  )
}
