import React, { useState } from 'react'
import ModalBtn from '../parts/ModalBtn'
import popopiyo from '../../img/.png'

const TitleBar = () => {
  const [isModalOpne, setIsModalOpne] = useState(false)

  const handleItemClick = () => {
    setIsModalOpne(true)
  }
  const closeModal = () => {
    setIsModalOpne(false)
  }

  return (
    <div className="sticky top-0 z-10 flex h-12 w-full items-center overscroll-contain bg-popoYellow pl-6 text-justify font-bold text-green-600 shadow xl:h-16">
      <a href="/popo2024sample/">
        <div className="transform rounded-full p-2 transition-transform duration-300 hover:scale-105 hover:bg-yellow-500">
          2024
        </div>
      </a>
      <button onClick={handleItemClick} className="ml-auto mr-10">
        <img src={popopiyo} alt="" className="h-10 py-1 xl:h-14" />
      </button>
      <ModalBtn modalIsOpen={isModalOpne} closeModal={closeModal} />
    </div>
  )
}

export default TitleBar
