import React, { useEffect, useRef, useState } from 'react'
import HTMLFlipBook from 'react-pageflip'
import Campus from '../img/Campus.jpg'
import loadingImg from '../img/loading.png'
import books from '../import/book'

const pages = books()
const Page = React.forwardRef((props, ref) => {
  return (
    <div className="demoPage bg-white " ref={ref}>
      <img src={Campus} alt="Campus" className="h-full w-full" />
    </div>
  )
})

const Book = () => {
  const [loading, setLoading] = useState(true)
  const book = useRef()
  const b5AspectRatio = 1 / Math.sqrt(2)
  const [bookSize, setBookSize] = useState({
    width: 0.7 * window.innerHeight * b5AspectRatio,
    height: 0.7 * window.innerHeight,
  })

  useEffect(() => {
    const handleResize = () => {
      setBookSize({
        width: window.innerHeight * 0.8 * b5AspectRatio,
        height: window.innerHeight * 0.8,
      })
    }

    // 
    window.addEventListener('resize', handleResize)

    // 
    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [b5AspectRatio])

  //
  const download = async () => {
    const imageUrls = books()

    try {
      await Promise.all(
        imageUrls.map((url) => {
          return new Promise((resolve) => {
            const images = new Image()
            images.src = url.note
            images.onload = () => {
              resolve(null)
            }
          })
        }),
      )
    } finally {
      // 
      setLoading(false)
    }
  }
  useEffect(() => {
    download()
  }, [])

  if (loading) {
    return (
      <div className="fixed left-1/2 top-1/2 z-50 -translate-x-1/2 -translate-y-1/2 transform">
        {/* <div className="h-10 w-10 animate-spin rounded-full border-4 border-popoYellow border-t-transparent"></div> */}
        <img
          src={loadingImg}
          alt=""
          className="h-10 w-10 animate-spin object-contain"
        />
      </div>
    )
  }

  return (
    <>
      <div className="w-creen relative ">
        <div className={` z-50 mt-20 flex flex-col items-center justify-start`}>
          <>
            <HTMLFlipBook
              width={450}
              height={635}
              minWidth={225}
              maxWidth={bookSize.width}
              minHeight={320}
              maxHeight={bookSize.height}
              flippingTime={500}
              showCover={true}
              size="stretch"
              mobileScrollSupport={true}
              startPage={0}
              ref={book}
              renderOnlyPageLengthChange={true}
            >
              <Page>Page 1</Page>
              {pages.map((item, index) => (
                <div className="container relative overflow-hidden bg-yellow-200">
                  {/* <div className="border-b-4 border-yellow-500 bg-popoYellow py-4 text-center text-xl ">
                    {item.title}
                  </div>
                  <div className="py-2 text-center">{item.describe}</div> */}

                  <img src={item.note} alt="" className="" />
                  {/* <div className="absolute bottom-5 left-8 z-10 text-base text-green-600 sm:bottom-8 sm:left-12 sm:text-2xl ">
                    p.{index + 1}
                  </div> */}
                </div>
              ))}
              <Page>Page 1</Page>
            </HTMLFlipBook>
          </>
          {/* <span>
            <button
              onClick={() => book.current.pageFlip().flipPrev()}
              className="m-6"
            >
              
            </button>

            <button
              onClick={() => book.current.pageFlip().flipNext()}
              className="m-6"
            >
              
            </button>
          </span>

          <span>
            <button
              onClick={() => book.current.pageFlip().flip(0)}
              className="m-6"
            >
              
            </button>
            <button
              onClick={() => book.current.pageFlip().flip(pages.length)}
              className="m-6"
            >
              
            </button>
          </span> */}
        </div>
      </div>
    </>
  )
}

export default Book
